.modal{
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}

.active{
    opacity: 1;
    pointer-events: all;
}

.modal_content{
    padding: 20px;
    border-radius: 10px;
    /* background-color: white; */
    max-width: 70vw;
    max-height: 100%;
    transform: scale(0.5);
    transition: 0.4s all;
    /* display: flex;
    justify-content: center;
    flex-direction: column; */
}

.modal_content.active{
    transform: scale(1);
}